import React, { Component } from 'react';
import {Map} from './Map';
import {ISOCODES} from './regions';
import CI4DATA from './data/CI4_2018_05_all.json';
import CI5DATA from './data/CI5_2018_05_all.json';

function padTh(num) {
    let ret = num.toString();
    while(ret.length < 3) {
        ret = '0' + ret;
    }
    return ret;
}

function formatNum(num) {
    const mil = num / 1000000;
    const th = (num / 1000) % 1000;
    const rest = num % 1000;
    return [mil, th, rest]
        .map(Math.round)
        .reduce((acc, el)=> ((el || acc.length) ? [...acc, acc.length ? padTh(el) : el] : acc), [])
        .join(' ');
}

const KOATU_OVERRIDE = {
    '43': '01', //    43 (   01)
    '09': '44', //    09 (   44)
    '77': '73', //    77 (   73)
    '30': '80', //    30 (   80)
    '40': '85', //    40 (   85)
};
const CI4_COLS = [
    'Total',
    'Quota',
    '   ',
    '   ',
    ',            100 ()   ',
    '   ,   ,   ',
    ',      ',
    ',  ()     ',
    ',    ,  ()   ,     ',
    ',     ',
    'Non-quota',
    '   ,    ,        2 ,   ,     ',
    ',       ,         ',
    ',          ',
    ',       ',
    ' ,   ,  ',
    ',            " "',
    ',       .  . 16 . 4  "       "',
];
const CI5_COLS = [
    '      ',
    ' .. ,   - ',
    '     ',
    '   ',
    '   ,     ',
    '   ,         ',
    ',            100 ()   ',
    ',      ,   ,     ',
    ',      ',
    ',  ()     ',
    ',                ,     ',
    '  ,    ,         ,   ,     ',
    ',               ',
    ',          ',
    ',       ',
    ' ,   ,  ',
    '    ',
    ',    3  .   28.03.2012  251',
    '  ,    14, 16, . 1  22 .   28.03.2012  251',
    '        " "',
    '.  . 16 . 4  "     "',
    '      ',
    '   ',
    '   ',
    '      ',
    ' ',
    '    ',
];

class CI4 extends Component {
    render() {
        const {line} = this.props;
        const [code, ...numbers] = line;
        return (<table>
            <tbody>
                {numbers.map((num, idx)=> (<tr key={idx}>
                    <td width={700}>{CI4_COLS[idx]}</td>
                    <td><strong>{formatNum(num) || '--'}</strong></td></tr>))}
            </tbody>
        </table>);
    }
}

class CI5 extends Component {
    render() {
        const {line} = this.props;
        const [code, ...numbers] = line;
        return (<table>
            <tbody>
                {numbers.map((num, idx)=> (<tr key={idx}>
                    <td width={700}>{CI5_COLS[idx]}</td>
                    <td><strong>{formatNum(num) || '--'}</strong></td></tr>))}
            </tbody>
        </table>);
    }
}

function koatu(iso) {
    const isoN = iso.substr(3);
    const region = KOATU_OVERRIDE[isoN] || isoN;
    return `${region}01`;
}

const CONTAINER = {
    display: 'flex',
};


class Selected extends Component {
    render() {
        const {iso} = this.props;
        const region = ISOCODES[iso];
        const ci4 = CI4DATA[koatu(iso)];
        const ci5 = CI5DATA[koatu(iso)];

        if (!ci4 && !ci5) {
            return (<div>No Data</div>);
        }

        return (<div>
            <h2>{region.title}</h2>
            ISO:{iso} :{koatu(iso)}

            <section>
                <div>
                    <h2>    ,    </h2>
                    {ci4 && (<CI4 line={ci4} />)}
                </div>

                <div>
                    <h2>           </h2>
                    {ci5 && (<CI5 line={ci5} />)}
                </div>
            </section>
        </div>);
    }
}

export class MapData extends Component {
    state = {
        selected: null,
    };

    handleSelect = (iso)=> {
        this.setState({selected: iso});
    };

    render() {
        const {selected} = this.state;
        return (<div style={CONTAINER}>
            <Map highlight={selected} onSelect={this.handleSelect} />
            <div>
                {selected && (<Selected iso={selected} />)}
            </div>
        </div>);
    }
}



